.App {
  box-sizing: border-box;
  padding:0;
  margin:0;

  top:0;
  left:0;

  width: 100%;
  height: 100%;
}

.image-container {
  display: flexbox;
}

.image-container img{
  max-height: 30vh;
  min-height: 30vh;
  clip-path: circle();
}

.App-header {
  background-color: #0c1536;
  min-height: 100vh;
  display: flow;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #91fdc3;;

}

.title{
  color: #91a1fd;
}

.subtitle {
  color: #c45148;

}

.bar{
  background-color: #282a31;
}

@media screen and (max-width:632px){
  /* For mobile phones: */
  .about {
    display:inline;
  }
}

.about{
  display:inline;
}

.fa {
  padding: 10px;
  font-size: 30px;
  width: 30px;
  text-align: center;
  text-decoration: none;
  border-radius: 50%;
}

.fa:hover {
    opacity: 0.7;
}

.fa-linkedin {
  background: #0072b1;
  color: white;
}

.fa-github {
  background: black;
  color: white;
}

.fa-itchio {
  background: #fa5c5c;
  color:white;
}